//IPAD PRO 1024px


@media screen and (max-width: 991px) {
    .loader {
        .container {
            width: 750px; } }


    .devil {
        &_result {
            width: 400px;
            top: -193px; } }
    .popup {
        .modal {
            width: 60%;
            &__result {
                &-img {
                    &_gameOver {
                        padding: 0;
                        padding-top: 10px; } } } } }
    .button_result {
        font-size: 16px; }
    .popup__shine {
        background-repeat: no-repeat;
        background-size: 100%; }
    .game {
        .container {
            width: 750px; } }
    .dialog {
        width: 230px;
        top: -250px;
        bottom: auto;
        right: -70px;
        z-index: 7;
        &_menu {
            min-width: 200px;
            min-height: auto;
            height: auto;
            font-size: 12px;
            span {
                font-size: 12px; } }

        &_victory {
            top: -250px;
            right: -100px; }
        &_gameOver {
            .dialog__text {
                font-size: 11px; } } }

    .shadow {
        height: 60%; }
    .popup {
        &__shine {
            width: 100%;
            height: 100%; } }
    .modal {
        &__frame {
            min-height: 272px; }
        &__result-img {
            &_gameOver {
                min-height: auto; }
            &-block {
                min-height: 240px;
                &_gameOver {
                    min-height: 122px; } } } } }




//IPAD
@media (max-width: 768px) {
    .languages {
        top: 2%; }
    .loader {
        .container {
            height: 100%;
            width: 100%; } }

    .modal {
        &__text-block {
            min-width: 267px; } }
    .modal {
        &__frame {}
        &__result-img {
            &_gameOver {
                min-height: 112px; }
            &-block {
                min-height: 240px;
                &_gameOver {
                    min-height: 122px; } } } }


    .modal__buttons-block_result {
        margin-top: 10px; }
    .gift-block {
        display: flex;
        justify-content: center;
        &__img {
            min-width: 100px;
            min-height: 100px;
            margin-top: 20px; } }
    .modal__candy-block {
        width: 110px;
        min-height: 100px;
        height: 140px;
        min-width: 0;
        .candy-bag {
            &__bag {}
            &__pumpkin {
                width: 80px; }
            &__title {
                font-size: 10px; } } }



    .candy-bag {
        &-modal {
            &__candy-img {
                width: 50px; }
            &__candies-grid {
                grid-gap: 8px;
                grid-template-columns: 55px 55px 55px 55px;
                grid-template-rows: 55px 55px; } }
        &__not-found {
            padding: 15px;

            &-descr {
                line-height: 1.4; }
            svg {
                display: none; } } }
    .candy-bag-modal__collection {
        height: 140px; }

    .popup {
        .modal {
            width: 70%;
            height: auto;
            min-height: auto; } }
    .footer {
        &__block {
            max-width: 100%;
            padding: 0 25px; } }
    .shadow-block {
        display: none; }
    .main-menu {
        &__logo {
            top: 15%;
            transform: translate(-50%, -50%);
            width: 300px; }
        .modal {
            width: 450px;
            padding-top: 50px;
            &__text-block {
                font-size: 12px; }
            .button {
                min-width: 31%;
                font-size: 14px;
                &__check-icon {
                    margin-left: 4px; } } }
        &__overlay {
            background-repeat: no-repeat;
            background-size: 100%; } }

    .dialog {
        width: 200px;
        &_menu {
            top: -230px;
            font-size: 12px;
            min-width: 150px;
            span {
                font-size: 12px; } }
        &_victory {
            right: -40px; }
        &_gameOver {
            .dialog__img {
                width: 200px; }
            .dialog__text {
                font-size: 10px; } } }
    .bottom-line {
        width: 550px; }
    .progress-bar {
        width: 300px; }
    .devil {
        &__image {
            // width: 100%
            &_menu {
                top: -130px;
                width: 400px; }
            &_up {
                top: 100px; } } }

    .shadow {
        height: 70%; }
    .game {
        height: 950px;
        width: 100%;
        .container {
            width: 600px;
            height: 100%; } }
    .card-list {
        &__block {
            grid-gap: 5px;
            &_simple {
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr;
                width: 500px;
                height: 500px; }
            &_regular {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr;
                width: 550px;
                height: 275px; }
            &_expert {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
                width: 550px;
                height: 400px; } } } }



@media only screen and (max-width: 575px) {
    .modal__buttons-block_result {
        margin-top: 0px; }
    .popup {
        .modal {
            &__frame {
                &_gameOver {
                    padding: 5px; } } } }

    .shadow {
        height: 80%; }
    .bottom-line {
        width: 90%;
        .progress-bar {
            width: 50%; } }
    .dialog {
        &_menu {
            span {
                font-size: 12px;
                line-height: 0.5; } }
        &_victory {
            width: 160px; }
        &_gameOver {
            width: 180px;
            right: -40px;
            .dialog__img {
                width: 180px; }
            .dialog__text {
                font-size: 9px; } }
        &__text {
            &_victory {
                font-size: 12px; } } }

    .devil {
        &__image {
            width: 100%;
            &_up {
                top: 15%; }
            &_menu {
                top: -130px; } }
        &_result {
            &_victory {
                top: -155px;
                width: 320px; }
            &_gameOver {
                width: 320px;
                top: -157px; } } }
    .modal {
        width: 90%;
        padding: 15px;
        &__result-img {
            width: 100%;
            &-block {
                min-height: auto; } }
        &__buttons-block {
            width: 100%;
            .button {
                min-width: 0;
                font-size: 14px; } }
        &__frame {
            min-height: auto; } }


    .card-list {
        &__block {
            grid-gap: 5px;
            &_simple {
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr;
                width: 400px;
                height: 400px; }
            &_regular {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr;
                width: 400px;
                height: 325px; }
            &_expert {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
                width: 400px;
                height: 400px; } } }
    .game {
        .container {
            width: 100%; } } }



//IPHONE_X IPHONE_6/7/8 PLUS
@media only screen and (max-width: 480px) {
    .languages {
        top: 1%;
        &__button {
            height: 25px;
            min-width: 40px !important; } }

    .gift-block {
        margin-top: -50px;
        &__title {
            font-size: 10px; } }
    .modal {
        &__main-block {
            justify-content: space-between; }
        &__candy-block {
            width: 100%; }
        &__text-block {
            min-width: 204px; } }


    .candy-bag-modal {
        &__candies-grid {
            grid-gap: 8px;
            grid-template-columns: 55px 55px 55px;
            grid-template-rows: 55px 55px; }
        &__not-found {
            &-title {
                font-size: 10px; }
            &-descr {
                font-size: 9px; } } }
    .popup {
        .modal {
            width: 90%;
            &_result {
                top: 35%; } }

        &__overlay {
            background: url('../../images/background-min.jpg') center center; } }
    .footer {
        display: none; }

    .dialog {
        &_menu {
            width: 150px;
            z-index: 7;
            top: -180px;
            right: -30px;
            padding: 10px;
            font-size: 9px;
            span {
                display: inline-block;
                font-size: 9px;
                line-height: 1.4; } }
        &_victory {
            padding: 15px;
            width: 140px;
            right: -5px;
            top: -220px; }
        &__text {
            &_victory {
                font-size: 12px; } } }

    .modal {
        &__tutorial-text {
            font-size: 11px; }
        &__buttons-block {
            flex-direction: row;
            .button {
                font-size: 14px;
                display: flex;
                align-items: center;
                flex-direction: column;
                &:last-child {
                    margin-bottom: 0; }
                &__check-icon {
                    margin-left: 0;
                    margin-top: 7px; } } } }
    .devil {
        &__image {
            top: 40%;
            transform: translate(-50%, -50%);
            width: 90%;
            opacity: 0;
            &_up {
                top: 20%;
                opacity: 1; }
            &_menu {
                opacity: 1;
                top: -30px;
                transform: translate(-50%, -50%);
                width: 375px; } }
        &_result {
            &_victory {
                top: -165px;
                width: 340px; }
            &_gameOver {
                top: -128px;
                width: 260px; } } }

    .main-menu {
        &__logo {
            width: 55%;
            top: 15%;
            transform: translate(-50%, -50%); }

        .modal {
            width: 90%;
            top: 45%;
            padding: 15px;
            padding-top: 40px;
            &__buttons-block {
                flex-direction: row; }
            .button {
                height: 100%;
                padding: 15px;
                width: 100%;
                font-size: 14px;
                margin-right: 8px;
                &:last-child {
                    margin-right: 0; } } }
        &__overlay {
            background-image: none; } }

    .timer {
        line-height: 1.5;
        left: 50%;
        transform: translateX(-50%); }
    .progress-bar {

        width: 60%; }
    .bottom-line {
        width: 90%;
        bottom: 10px;
        &__buttons {
            .button {
                padding: 15px;
                height: 100%;
                font-size: 12px;
                margin-right: 5px;
                &:last-child {
                    margin-right: 0; } } } }
    .shadow {
        display: none; }
    .game {
        height: 100%;
        .container {
            background: $dark-brown; } }
    .card-list {
        padding-top: 45%;
        &__block {
            background: $dark-brown;
            box-shadow: 0 -25px 25px $dark-brown;
            grid-gap: 5px;
            &_simple {
                grid-template-columns: 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr;
                width: 325px;
                height: 250px; }
            &_regular {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
                width: 325px;
                height: 325px; }
            &_expert {
                grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
                grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
                width: 325px;
                height: 400px; } } }
    .modal {
        &__frame {}
        &__result-img {
            min-height: 0;
            &-block {
                min-height: auto;
                &_gameOver {} } } } }



